      getArea=()=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        if(loginid!='' && user!=""){
          axios.post('/index.php/pro/getarea',{
            name:user,
            num:loginid
          })
          .then((res)=>{
            var arr=res.data.data
            constlist =Object.keys(arr).map((item, index) =>({ value:arr[item],label: item}));
               console.log(list);
            list.map(ite => {
              constshi =Object.keys(ite.value).map((j, index) =>({ value:j,label: j}));
               console.log(shi);
               this.state.obj.push({
                'value':ite.label,
            'label':ite.label,
                'children': shi
              })
             
            })
            console.log(this.state.obj);
          }).catch((error)=>{
          console.log(error)
          })
        }

      }
      onChangeSelect(value){
        console.log(value);
        localStorage.setItem('newslou',value);
     }
